.change-form-container-new .edit-detail-resp-container .detail-inner-resp-container .detail-resp-value, .edit-detail-value-container-new .edit-detail-value .detail-value {
  font-size: 14px;
  font-weight: bold;
  color: #2C3652; }

.change-form-container-new {
  display: flex;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  padding: 10px 20px 16px 20px;
  margin: 16px 0; }
  @media screen and (max-width: 768px) {
    .change-form-container-new {
      padding: 15px;
      margin: 8px 0; } }
  .change-form-container-new .edit-detail-container {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%; }
    .change-form-container-new .edit-detail-container .edit-detail-inner-container {
      display: flex; }
    .change-form-container-new .edit-detail-container .edit-detail-label-container,
    .change-form-container-new .edit-detail-container .edit-detail-value-container {
      display: flex;
      flex-direction: column; }
  .change-form-container-new .edit-detail-resp-container {
    flex: 1;
    display: flex;
    align-items: center; }
    .change-form-container-new .edit-detail-resp-container .detail-inner-resp-container {
      flex: 1; }
      .change-form-container-new .edit-detail-resp-container .detail-inner-resp-container .edit-detail-value-container {
        display: flex;
        flex-wrap: wrap; }
      .change-form-container-new .edit-detail-resp-container .detail-inner-resp-container .detail-resp-value {
