.single-check-box-container {
  position: relative;
  z-index: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: 1px solid #CADBEA;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 7px 10px 7px 10px;
  transition: border 0.3s ease; }
  @media screen and (max-width: 768px) {
    .single-check-box-container {
      padding: 6px; } }
  .single-check-box-container .bg {
    z-index: -1;
    position: absolute;
    border-radius: 4px;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    transition: background-color 0.3s ease, transform 0.3s ease;
    transform-origin: left; }
  .single-check-box-container .box {
    margin-right: 6px;
    width: 14px;
    height: 14px;
    background-color: white;
    box-sizing: border-box;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: border 0.3s ease; }
    .single-check-box-container .box img {
      transition: transform 0.3s ease; }
  .single-check-box-container .desc {
    font-weight: 600;
    font-size: 12px;
    text-align: center;
    margin: 0;
    transition: color 0.5s ease; }
    @media screen and (max-width: 768px) {
      .single-check-box-container .desc {
        font-size: 8px; } }

.single-check-box-checked {
  border: 1px solid white; }
  .single-check-box-checked .bg {
    background-color: #6480EB;
    transform: scaleX(1); }
  .single-check-box-checked .box {
    border: none; }
    .single-check-box-checked .box img {
      transform: scale(1); }
  .single-check-box-checked .desc {
    color: white; }

.single-check-box-non-checked {
  border: 1px solid #CADBEA; }
  .single-check-box-non-checked .bg {
    background-color: white;
    transform: scaleX(0); }
  .single-check-box-non-checked .box {
    border: 1px solid #CADBEA; }
    .single-check-box-non-checked .box img {
      transform: scale(0); }
  .single-check-box-non-checked .desc {
    color: #6E7F95; }
